<template>
  <div id="count">
      <a href="" class="myButton" v-on:click.stop="addCount()">Call Api</a>
      <div><span>{{ count }}</span></div>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  data () {
    return {
        count: "",
        pagecount: "/pagecount",
        addcount: "/addcount",
        endpoint: process.env.WEBMONGO_URL || 'http://nodejs-mongodb-example-bmpibit-example.apps.us-west-2.online-starter.openshift.com'
    }
  },

  mounted() {
    this.pageCount();
  },

  methods: {
    addCount() {
      console.log("[OpenShiftVue - addCount]  Url connection string: ", this.endpoint + this.addcount);
      axios.get(this.endpoint + this.addcount, {crossDomain: true})
        .then(response => {
          this.count = response.data;
          console.log("[OpenShiftVue - addCount Then]  Data: ", response.data );
        })
        .catch(error => {
          console.log('-----error-------');
          console.log(error);
        })
    },
    pageCount() {
      console.log("[OpenShiftVue - pageCount]  Url connection string: ", this.endpoint + this.pagecount);
      axios.get(this.endpoint + this.pagecount, {crossDomain: true})
        .then(response => {
          this.count = response.data;
           console.log("[OpenShiftVue - pageCount Then]  Data: ", response.data);
        })
        .catch(error => {
          console.log('-----error-------');
          console.log(error);
        })
    }
  }
}
</script>
<!-- styling for the component -->
<style>
#count {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
.myButton {
	-moz-box-shadow: 0px 10px 14px -7px #3e7327;
	-webkit-box-shadow: 0px 10px 14px -7px #3e7327;
	box-shadow: 0px 10px 14px -7px #3e7327;
	background:-webkit-gradient(linear, left top, left bottom, color-stop(0.05, #77b55a), color-stop(1, #72b352));
	background:-moz-linear-gradient(top, #77b55a 5%, #72b352 100%);
	background:-webkit-linear-gradient(top, #77b55a 5%, #72b352 100%);
	background:-o-linear-gradient(top, #77b55a 5%, #72b352 100%);
	background:-ms-linear-gradient(top, #77b55a 5%, #72b352 100%);
	background:linear-gradient(to bottom, #77b55a 5%, #72b352 100%);
	filter:progid:DXImageTransform.Microsoft.gradient(startColorstr='#77b55a', endColorstr='#72b352',GradientType=0);
	background-color:#77b55a;
	-moz-border-radius:4px;
	-webkit-border-radius:4px;
	border-radius:4px;
	border:1px solid #4b8f29;
	display:inline-block;
	cursor:pointer;
	color:#ffffff;
	font-family:Arial;
	font-size:13px;
	font-weight:bold;
	padding:6px 12px;
	text-decoration:none;
	text-shadow:0px 1px 0px #5b8a3c;
}
.myButton:hover {
	background:-webkit-gradient(linear, left top, left bottom, color-stop(0.05, #72b352), color-stop(1, #77b55a));
	background:-moz-linear-gradient(top, #72b352 5%, #77b55a 100%);
	background:-webkit-linear-gradient(top, #72b352 5%, #77b55a 100%);
	background:-o-linear-gradient(top, #72b352 5%, #77b55a 100%);
	background:-ms-linear-gradient(top, #72b352 5%, #77b55a 100%);
	background:linear-gradient(to bottom, #72b352 5%, #77b55a 100%);
	filter:progid:DXImageTransform.Microsoft.gradient(startColorstr='#72b352', endColorstr='#77b55a',GradientType=0);
	background-color:#72b352;
}
.myButton:active {
	position:relative;
	top:1px;
}
</style>


